import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import Home from '../views/Home.vue';
import Course from '../views/Course/Course.vue';
import Movie from '../views/Course/Movie.vue';
import PointBuy from '../views/PointBuy/PointBuy.vue';
import ShoppingCart from '../views/PointBuy/ShoppingCart.vue';
import Schedule from '../views/Schedule/Schedule.vue';
import Unit from '../views/Schedule/Unit.vue';
import AccountManage from '../views/AccountManage/AccountManage.vue';
import BuyRecord from '../views/AccountManage/BuyRecord.vue';
import PointStatus from '../views/AccountManage/PointStatus.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/course',
    name: 'course',
    component: Course,
  },
  {
    path: '/course/:id',
    name: 'movie',
    component: Movie,
  },
  {
    path: '/pointBuy',
    name: 'pointBuy',
    component: PointBuy,
  },
  {
    path: '/schedule',
    name: 'schedule',
    component: Schedule,
  },
  {
    path: '/unit/:id',
    name: 'Unit',
    component: Unit,
  },
  {
    path: '/accountManagement',
    name: 'accountManagement',
    component: AccountManage,
  },
  {
    path: '/buyRecord',
    name: 'buyRecord',
    component: BuyRecord,
  },
  {
    path: '/pointStatus',
    name: 'pointStatus',
    component: PointStatus,
  },
  {
    path: '/shoppingCart',
    name: 'shoppingCart',
    component: ShoppingCart,
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    return savedPosition ? savedPosition : { top: 0 };
  },
});

export default router;
